.background-icon {
  width: 1531px;
  height: 727px;
  position: relative;
  object-fit: cover;
  display: none;
  max-width: 100%;
  z-index: 0;
}
.bullish-brains {
  position: relative;
  z-index: 2;
}
.label-text {
  width: 36px;
