  letter-spacing: 0.1px;
  line-height: 20px;
  font-weight: 500;
  display: none;
}
.state-layer {
  height: 47px;
  flex: 1;
  background-color: var(--m3-state-layers-light-on-primary-opacity-008);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: var(--padding-3xs) var(--padding-5xl);
  box-sizing: border-box;
}
.button,
.home {
  position: absolute;
  display: flex;
}
.button {
  top: 0;
  left: 0;
  box-shadow: var(--m3-elevation-light-1);
  border-radius: var(--br-mini);
  background-color: var(--color-darkviolet-200);
  width: 100%;
  height: 100%;
  overflow: hidden;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  z-index: 2;
}
.home {
  text-decoration: none;
  top: 8px;
  left: 15px;
  font-size: var(--font-size-10xl);
  line-height: 31px;
  font-weight: 600;
  font-family: var(--font-inter);
  color: inherit;
  text-align: left;
  align-items: center;
  width: 86px;
  height: 31px;
  min-width: 86px;
  z-index: 3;
}
.button-parent {
  height: 47px;
  width: 110px;
  position: relative;
}
