.button-states1 {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
  height: 47px;
  width: 153px;
  position: relative;
}
.home-button {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-24xl);
}
.authors,
.header {
  display: flex;
  max-width: 100%;
}
.authors {
  width: 486px;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  padding: 0 0 var(--padding-12xs);
  box-sizing: border-box;
  text-align: center;
  font-size: var(--m3-label-large-size);
  color: var(--schemes-on-primary);
  font-family: var(--m3-label-large);
}
.header {
  align-self: stretch;
  flex-direction: row;
  align-items: flex-end;
  justify-content: space-between;
  gap: var(--gap-xl);
}
.content-child {
  position: absolute;
  top: -15px;
  left: 0;
  border-radius: 20px;
  width: 1531px;
  height: 103px;
  z-index: 1;
}
.image-icon2 {
  position: absolute;
  top: 28px;
  left: 34px;
  width: 33px;
  height: 33px;
  object-fit: contain;
  z-index: 2;
}
.content {
  width: 100%;
  height: 88px;
  position: absolute;
  margin: 0 !important;
  top: 0;
  right: 0;
  left: 0;
}
.unlock-the-power,
.with-our-comprehensive {
  margin: 0;
}
.learning-platfor,
.unlock-the-power-container {
  margin: 0;
  position: relative;
  font-size: inherit;
  font-family: inherit;
  z-index: 1;
}
.unlock-the-power-container {
  align-self: stretch;
  font-weight: 700;
}
.learning-platfor {
  width: 472px;
  line-height: 48px;
  font-weight: 600;
  display: flex;
  align-items: center;
  flex-shrink: 0;
  max-width: 100%;
}
.button3 {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  padding: 0 var(--padding-xl) 0 118px;
  box-sizing: border-box;
  max-width: 100%;
  text-align: left;
  font-size: var(--font-size-26xl);
  color: var(--color-darkviolet-200);
}
.unlock-the-power-parent {
  width: 942px;
  flex-direction: column;
  justify-content: flex-start;
  gap: var(--gap-38xl);
  max-width: 100%;
}
.course-info,
.figma-design-1png,
.unlock-the-power-parent {
  display: flex;
  align-items: flex-start;
}
.course-info {
  width: 1342px;
  flex-direction: row;
  justify-content: center;
  max-width: 100%;
  text-align: center;
  font-size: 49px;
  color: #111;
  font-family: var(--font-inter);
}
.figma-design-1png {
  width: 100%;
  position: relative;
  overflow: hidden;
  flex-direction: column;
  justify-content: flex-start;
  padding: 19px 82px 239px;
  box-sizing: border-box;
  gap: 198px;
  background-image: url(/public/background@2x.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
  line-height: normal;
  letter-spacing: normal;
  text-align: left;
  font-size: 32px;
  color: #a21ef3;
  font-family: var(--font-croissant-one);
}
@media screen and (max-width: 850px) {
  .bullish-brains {
    font-size: var(--font-size-7xl);
  }
  .header {
    flex-wrap: wrap;
  }
  .unlock-the-power-container {
    font-size: 39px;
  }
  .learning-platfor {
    font-size: 36px;
    line-height: 38px;
  }
  .figma-design-1png {
    gap: 99px;
    padding-left: var(--padding-22xl);
    padding-right: var(--padding-22xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 675px) {
  .home-button {
    flex-wrap: wrap;
    gap: 21px;
  }
  .button3 {
    padding-left: 59px;
    box-sizing: border-box;
  }
  .unlock-the-power-parent {
    gap: var(--gap-9xl);
  }
}
@media screen and (max-width: 450px) {
  .bullish-brains {
    font-size: var(--font-size-lgi);
  }
  .home,
  .home1,
  .home2 {
    font-size: var(--font-size-4xl);
    line-height: 25px;
  }
  .home1,
  .home2 {
    line-height: 21px;
  }
  .home2 {
    font-size: var(--font-size-lgi);
  }
  .unlock-the-power-container {
    font-size: var(--font-size-10xl);
  }
  .learning-platfor {
    font-size: 27px;
    line-height: 29px;
  }
  .button3 {
    padding-left: var(--padding-xl);
    box-sizing: border-box;
  }
  .figma-design-1png {
    gap: 49px;
  }
}
