.image-icon {
  position: absolute;
  right: 1326px;
  bottom: 25px;
  width: 33px;
  height: 33px;
  object-fit: contain;
}
.groups {
  height: 81px;
  flex: 1;
  position: relative;
  background-color: var(--color-gray-400);
  max-width: 100%;
}
.groups-wrapper {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 0 var(--padding-xl);
  box-sizing: border-box;
  max-width: 100%;
}
.and-trading-from,
.fundamentals-of,
.learn-the,
.scratch,
.stock-markets {
  margin: 0;
}
.learn-the-funda {
  align-self: stretch;
  position: relative;
  font-weight: 600;
}
.fundas {
  width: 259px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  padding: 0 0 var(--padding-3xs);
  box-sizing: border-box;
}
.stock-market-and,
.your-gateway-to {
  margin: 0;
}
.your-gateway-to-container {
  margin: 0;
  position: relative;
  font-size: 50px;
  font-weight: 400;
  font-family: var(--font-marko-one);
  color: #9a0bf2;
  text-align: right;
  display: inline-block;
  max-width: 100%;
}
.benefit-one,
.benefits {
  display: flex;
  flex-direction: row;
  max-width: 100%;
}
.benefit-one {
  width: 1235px;
  align-items: flex-end;
  justify-content: space-between;
  gap: var(--gap-xl);
}
.benefits {
  align-self: stretch;
  align-items: flex-start;
  justify-content: flex-end;
  padding: 0 60px;
  box-sizing: border-box;
}
.image-icon1 {
  height: 428px;
  flex: 1;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  object-fit: cover;
}
.explore-various1,
.investment,
.strategies {
  margin: 0;
}
.explore-various-txt-container {
  width: 100%;
}
.explore-various {
  width: 239px;
  position: absolute;
  margin: 0 !important;
  top: 19px;
  right: -195px;
  font-weight: 600;
  display: flex;
  align-items: center;
  z-index: 1;
}
.exploration {
  width: 470px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  position: relative;
  min-width: 470px;
  max-width: 100%;
}
.effective-risk,
.learning-the,
.management {
  margin: 0;
}
.learning-the-ef-txt-container {
  width: 100%;
}
.learning-the-ef {
  width: 202px;
  position: relative;
  font-weight: 600;
  display: flex;
  align-items: center;
  flex-shrink: 0;
}
.developing,
.psychological,
.stamina {
  margin: 0;
}
.developing-psyc-txt {
  line-break: anywhere;
  width: 100%;
}
.developing-psyc {
  width: 203px;
  position: relative;
  font-weight: 600;
  display: flex;
  align-items: center;
}
.background {
  height: 39px;
  width: 247px;
  position: relative;
  background-color: var(--color-darkviolet-300);
  border: 1px solid var(--color-mediumorchid-200);
  box-sizing: border-box;
  display: none;
}
.backtoagendapage {
  flex: 1;
  position: relative;
  font-size: var(--font-size-base-5);
  font-weight: 600;
  font-family: var(--font-inter);
  color: #edd5fc;
  text-align: left;
  z-index: 1;
}
.back,
.back-link {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}
.back-link {
  cursor: pointer;
  border: 1px solid var(--color-mediumorchid-200);
  padding: var(--padding-7xs) var(--padding-xl) var(--padding-3xs)
    var(--padding-10xl);
  background-color: var(--color-darkviolet-300);
  width: 247px;
  box-sizing: border-box;
  justify-content: flex-start;
  white-space: nowrap;
}
.back-link:hover {
  background-color: var(--color-darkviolet-100);
  border: 1px solid #9447c4;
  box-sizing: border-box;
}
.back {
  align-self: stretch;
  justify-content: flex-end;
}
.development,
.effective,
.psychology {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.development {
  align-self: stretch;
  flex-direction: column;
  gap: var(--gap-19xl);
}
.effective,
.psychology {
  max-width: 100%;
}
.psychology {
  flex: 1;
  flex-direction: column;
  padding: 94px 0 0;
  box-sizing: border-box;
  min-width: 255px;
  font-size: 28px;
  color: #161616;
}
.effective {
  align-self: stretch;
  flex-direction: row;
  gap: 68px;
}
.benefit-two,
.learning {
  display: flex;
  box-sizing: border-box;
  max-width: 100%;
}
.learning {
  width: 663px;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  padding: 0 0 var(--padding-19xl);
  min-width: 663px;
  font-size: var(--font-size-7xl);
  color: #1b1b1b;
}
.benefit-two {
  width: 1379px;
  flex-direction: row;
  align-items: flex-end;
  justify-content: space-between;
  padding: 0 var(--padding-xl) 0 0;
  gap: var(--gap-xl);
  font-size: var(--font-size-5xl);
  color: #1a1a1a;
}
.figma-design-3png,
.root {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.root {
  flex: 1;
  background-color: var(--schemes-on-primary);
  flex-direction: column;
  padding: var(--padding-11xl) 0 0;
  box-sizing: border-box;
  gap: 11px;
  max-width: 100%;
  text-align: left;
  font-size: var(--font-size-4xl);
  color: #171717;
  font-family: var(--font-inter);
}
.figma-design-3png {
  width: 100%;
  position: relative;
  background-color: var(--color-gray-400);
  overflow: hidden;
  flex-direction: row;
  line-height: normal;
  letter-spacing: normal;
}
@media screen and (max-width: 1200px) {
  .benefit-one {
    flex-wrap: wrap;
  }
  .benefits {
    padding-left: var(--padding-11xl);
    padding-right: var(--padding-11xl);
    box-sizing: border-box;
  }
  .exploration,
  .image-icon1,
  .learning {
    flex: 1;
  }
  .benefit-two {
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 1050px) {
  .your-gateway-to-container {
    font-size: 40px;
  }
}
@media screen and (max-width: 750px) {
  .exploration {
    min-width: 100%;
  }
  .effective {
    flex-wrap: wrap;
    gap: 34px;
  }
  .learning {
    min-width: 100%;
  }
}
@media screen and (max-width: 450px) {
  .learn-the-funda {
    font-size: var(--font-size-lg);
  }
  .your-gateway-to-container {
    font-size: 30px;
  }
  .explore-various {
    font-size: var(--font-size-lgi);
  }
  .learning-the-ef {
    font-size: var(--font-size-2xl);
  }
  .developing-psyc {
    font-size: var(--font-size-3xl);
  }
  .development {
    gap: var(--gap-lgi);
  }
  .effective {
    gap: var(--gap-mid);
  }
}
