@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;600;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Marko+One:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Croissant+One:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Markazi+Text:wght@400&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-inter: Inter;
  --font-marko-one: "Marko One";
  --m3-label-large: Roboto;
  --font-croissant-one: "Croissant One";
  --font-markazi-text: "Markazi Text";

  /* font sizes */
  --font-size-base-5: 15.5px;
  --font-size-3xl: 22px;
  --font-size-7xl: 26px;
  --font-size-2xl: 21px;
  --font-size-5xl: 24px;
  --font-size-lgi: 19px;
  --font-size-4xl: 23px;
  --font-size-lg: 18px;
  --font-size-26xl: 45px;
  --font-size-10xl: 29px;
  --m3-label-large-size: 14px;

  /* Colors */
  --color-gray-100: #fefefe;
  --color-gray-200: #151515;
  --color-gray-300: #0c0c0c;
  --m3-state-layers-light-on-primary-opacity-008: rgba(255, 255, 255, 0.08);
  --color-gray-400: rgba(0, 0, 0, 0);
  --schemes-on-primary: #fff;
  --color-mediumorchid-100: #ca7ff9;
  --color-mediumorchid-200: #c87af8;
  --color-darkviolet-100: #ba26ff;
  --color-darkviolet-200: #9b0ef2;
  --color-darkviolet-300: #9500f2;
  --color-whitesmoke: #f4f4f4;
  --color-blueviolet-100: #a82ff2;
  --color-darkslategray-100: #303030;

  /* Gaps */
  --gap-xl: 20px;
  --gap-mid: 17px;
  --gap-19xl: 38px;
  --gap-lgi: 19px;
  --gap-38xl: 57px;
  --gap-9xl: 28px;
  --gap-24xl: 43px;
  --gap-20xl: 39px;
  --gap-8xs: 5px;

  /* Paddings */
  --padding-11xl: 30px;
  --padding-xl: 20px;
  --padding-19xl: 38px;
  --padding-7xs: 6px;
  --padding-3xs: 10px;
  --padding-10xl: 29px;
  --padding-22xl: 41px;
  --padding-12xs: 1px;
  --padding-5xl: 24px;
  --padding-13xl: 32px;
  --padding-2xl: 21px;
  --padding-37xl: 56px;
  --padding-21xl: 40px;
  --padding-4xs: 9px;
  --padding-9xl: 28px;

  /* Border radiuses */
  --br-mini: 15px;

  /* Effects */
  --m3-elevation-light-1: 0px 1px 2px rgba(0, 0, 0, 0.3),
    0px 1px 3px 1px rgba(0, 0, 0, 0.15);
}
