.background6 {
  height: 183px;
  width: 639px;
  position: relative;
  background-color: var(--color-whitesmoke);
  display: none;
  max-width: 100%;
}
.image-icon7 {
  height: 107px;
  width: 107px;
  position: relative;
  object-fit: contain;
  z-index: 1;
}
.briefly-elaborat2,
.write-the-second {
  position: relative;
  font-weight: 600;
  z-index: 1;
}
.write-the-second {
  align-self: stretch;
}
.briefly-elaborat2 {
  flex: 1;
}
.briefly-elaborat-wrapper {
  width: 318px;
  flex-direction: row;
  padding: 0 var(--padding-12xs);
  box-sizing: border-box;
  font-size: var(--font-size-3xl);
  color: var(--color-darkslategray-100);
}
.briefly-elaborat-wrapper,
.step-images-inner,
.step-images1,
.write-the-second-parent {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.write-the-second-parent {
  align-self: stretch;
  flex-direction: column;
  gap: var(--gap-8xs);
}
.step-images-inner,
.step-images1 {
  box-sizing: border-box;
  max-width: 100%;
}
.step-images-inner {
  width: 353px;
  flex-direction: column;
  padding: var(--padding-xl) 0 0;
}
.step-images1 {
  align-self: stretch;
  background-color: var(--color-whitesmoke);
  flex-direction: row;
  padding: var(--padding-19xl) var(--padding-37xl);
  gap: var(--gap-20xl);
  text-align: left;
  font-size: var(--font-size-7xl);
  color: var(--color-gray-200);
  font-family: var(--font-inter);
}
@media screen and (max-width: 750px) {
  .step-images1 {
    flex-wrap: wrap;
    gap: var(--gap-lgi);
    padding-left: var(--padding-9xl);
    padding-right: var(--padding-9xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .write-the-second {
    font-size: var(--font-size-2xl);
  }
  .briefly-elaborat2 {
    font-size: var(--font-size-lg);
  }
}
