.key {
  margin: 0;
  flex: 1;
  position: relative;
  font-size: inherit;
  font-weight: 400;
  font-family: inherit;
}
.key-wrapper {
  width: 211px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 4px;
  box-sizing: border-box;
}
.indicators,
.performance {
  margin: 0;
}
.performance-indi {
  margin: 0;
  align-self: stretch;
  position: relative;
  font-size: 74px;
  font-weight: 400;
  font-family: inherit;
  color: #070707;
}
.keys {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 12px;
}
.background4 {
  height: 39px;
  width: 246px;
  position: relative;
  background-color: var(--color-darkviolet-300);
  border: 1px solid var(--color-mediumorchid-100);
  box-sizing: border-box;
  display: none;
}
.backtoagendapage2 {
  flex: 1;
  position: relative;
  font-size: var(--font-size-base-5);
  font-weight: 600;
  font-family: var(--font-inter);
  color: #edd3fc;
  text-align: left;
  z-index: 1;
}
.background-group {
  cursor: pointer;
  border: 1px solid var(--color-mediumorchid-100);
  padding: var(--padding-7xs) var(--padding-xl) var(--padding-4xs)
    var(--padding-9xl);
  background-color: var(--color-darkviolet-300);
  width: 246px;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  white-space: nowrap;
}
.background-group:hover {
  background-color: var(--color-darkviolet-100);
  border: 1px solid #964dc7;
  box-sizing: border-box;
}
.image-icon5 {
  width: 33px;
  height: 33px;
  position: absolute;
  margin: 0 !important;
  top: 0;
  left: 1px;
  object-fit: contain;
}
.content1,
.keys-parent {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
}
.keys-parent {
  align-self: stretch;
  padding: 207px 0 0;
  position: relative;
  gap: 87px;
}
.content1 {
  width: 535px;
  padding: 0 0 3px;
  box-sizing: border-box;
  min-width: 535px;
  max-width: 100%;
}
.background5 {
  height: 183px;
  width: 638px;
  position: relative;
  background-color: var(--color-whitesmoke);
  display: none;
  max-width: 100%;
}
.image-icon6 {
  height: 107px;
  width: 107px;
  position: relative;
  object-fit: contain;
  z-index: 1;
}
.write-the-first {
  align-self: stretch;
  position: relative;
  font-weight: 600;
  z-index: 1;
}
.briefly-elaborat1 {
  flex: 1;
  position: relative;
  z-index: 1;
}
.elaborations,
.step-titles {
  align-self: stretch;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.elaborations {
  flex-direction: row;
  padding: 0 0 0 var(--padding-12xs);
  font-size: var(--font-size-3xl);
  color: #2a2a2a;
}
.step-titles {
  flex-direction: column;
  gap: 6px;
}
.step-details,
.step-images {
  align-items: flex-start;
  box-sizing: border-box;
}
.step-details {
  width: 317px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: var(--padding-xl) 0 0;
}
.step-images {
  align-self: stretch;
  background-color: var(--color-whitesmoke);
  flex-direction: row;
  padding: var(--padding-19xl) var(--padding-37xl);
  gap: var(--gap-20xl);
  max-width: 100%;
}
.figma-design-5png,
.root2,
.step-images,
.steps {
  display: flex;
  justify-content: flex-start;
}
.steps {
  flex: 1;
  flex-direction: column;
  align-items: flex-start;
  gap: var(--gap-38xl);
  min-width: 415px;
  max-width: 100%;
  font-size: var(--font-size-7xl);
  color: var(--color-gray-200);
  font-family: var(--font-inter);
}
.figma-design-5png,
.root2 {
  flex-direction: row;
}
.root2 {
  flex: 1;
  background-color: var(--schemes-on-primary);
  align-items: flex-end;
  padding: 50px 80px 44px;
  box-sizing: border-box;
  gap: 105px;
  max-width: 100%;
  flex-shrink: 0;
  text-align: left;
  font-size: 75px;
  color: var(--color-gray-300);
  font-family: var(--font-marko-one);
}
.figma-design-5png {
  width: 100%;
  height: 734px;
  position: relative;
  background-color: var(--color-gray-400);
  overflow: hidden;
  align-items: flex-start;
  line-height: normal;
  letter-spacing: normal;
}
@media screen and (max-width: 1200px) {
  .content1 {
    flex: 1;
  }
  .root2 {
    flex-wrap: wrap;
    gap: 52px;
    padding-left: var(--padding-21xl);
    padding-right: var(--padding-21xl);
    box-sizing: border-box;
  }
  .figma-design-5png {
    height: auto;
  }
}
@media screen and (max-width: 1050px) {
  .key {
    font-size: 60px;
  }
  .performance-indi {
    font-size: 59px;
  }
}
@media screen and (max-width: 750px) {
  .keys-parent {
    gap: var(--gap-24xl);
    padding-top: 135px;
    box-sizing: border-box;
  }
  .content1 {
    min-width: 100%;
  }
  .step-images {
    flex-wrap: wrap;
    gap: var(--gap-lgi);
    padding-left: var(--padding-9xl);
    padding-right: var(--padding-9xl);
    box-sizing: border-box;
  }
  .steps {
    gap: var(--gap-9xl);
    min-width: 100%;
  }
  .root2 {
    gap: 26px;
    padding-top: var(--padding-13xl);
    padding-bottom: var(--padding-10xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .key {
    font-size: var(--font-size-26xl);
  }
  .performance-indi {
    font-size: 44px;
  }
  .keys-parent {
    gap: 22px;
  }
  .write-the-first {
    font-size: var(--font-size-2xl);
  }
  .briefly-elaborat1 {
    font-size: var(--font-size-lg);
  }
}
