.background1 {
  width: 624px;
  height: 733px;
  position: relative;
  background-color: var(--color-whitesmoke);
  display: none;
  max-width: 100%;
}
.image-icon3 {
  width: 33px;
  height: 34px;
  position: relative;
  object-fit: contain;
}
.report,
.trend {
  margin: 0;
  position: relative;
  font-weight: 400;
}
.trend {
  font-size: inherit;
  line-height: 102px;
  font-family: inherit;
}
.report {
  align-self: stretch;
  font-size: 77px;
  font-family: var(--font-marko-one);
  color: #060606;
}
.report-options,
.report-title {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.report-options {
  flex: 1;
  flex-direction: column;
}
.report-title {
  width: 290px;
  flex-direction: row;
  padding: 0 var(--padding-12xs);
  box-sizing: border-box;
  font-family: var(--font-markazi-text);
}
.observation,
.write-a-significant {
  margin: 0;
}
.write-a-signific-container {
  flex: 1;
  position: relative;
  font-weight: 600;
  display: inline-block;
  max-width: 100%;
}
.write-a-signific-wrapper {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 0 var(--padding-2xl);
  box-sizing: border-box;
  max-width: 100%;
  font-size: var(--font-size-2xl);
  color: #191919;
}
.briefly-elaborate-on,
.discuss {
  margin: 0;
}
.briefly-elaborat {
  align-self: stretch;
  position: relative;
  font-weight: 600;
}
.background2 {
  height: 38px;
  width: 247px;
  position: relative;
  background-color: var(--color-darkviolet-300);
  border: 1px solid var(--color-blueviolet-100);
  box-sizing: border-box;
  display: none;
}
.backtoagendapage1 {
  flex: 1;
  position: relative;
  font-size: var(--font-size-base-5);
  font-weight: 600;
  font-family: var(--font-inter);
  color: #edd4fc;
  text-align: left;
  z-index: 1;
}
.background-parent {
  cursor: pointer;
  border: 1px solid var(--color-blueviolet-100);
  padding: var(--padding-7xs) var(--padding-xl) var(--padding-4xs)
    var(--padding-9xl);
  background-color: var(--color-darkviolet-300);
  width: 247px;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  white-space: nowrap;
}
.background-parent:hover {
  background-color: var(--color-darkviolet-100);
  border: 1px solid #c247ff;
  box-sizing: border-box;
}
.briefly-elaborat-parent,
.groups1,
.report-title-parent {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.briefly-elaborat-parent {
  align-self: stretch;
  gap: 35px;
  font-size: 18.2px;
  color: #3a3a3a;
}
.groups1,
.report-title-parent {
  width: 420px;
  gap: 29px;
  max-width: 100%;
}
.groups1 {
  width: 630px;
  background-color: var(--color-whitesmoke);
  padding: var(--padding-37xl) 81px var(--padding-19xl);
  box-sizing: border-box;
  gap: 154px;
  min-width: 630px;
  z-index: 1;
}
.background3 {
  height: 734px;
  width: 815px;
  position: relative;
  background-color: var(--color-gray-100);
  display: none;
  max-width: 100%;
}
.image-icon4 {
  position: absolute;
  right: 149px;
  bottom: 80px;
  width: 516px;
  height: 502px;
  object-fit: cover;
}
.groups2 {
  height: 589px;
  width: 689px;
  position: relative;
  background-color: var(--color-gray-400);
  max-width: 100%;
  z-index: 1;
}
.figma-design-4png,
.gallery,
.root1 {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}
.gallery {
  flex: 1;
  background-color: var(--color-gray-100);
  justify-content: flex-end;
  padding: 113px 5px var(--padding-13xl);
  box-sizing: border-box;
  min-width: 530px;
  max-width: 100%;
  margin-left: -5px;
}
.figma-design-4png,
.root1 {
  justify-content: flex-start;
}
.root1 {
  flex: 1;
  background-color: #d3d3d3;
  row-gap: 20px;
  max-width: 100%;
  text-align: left;
  font-size: 110px;
  color: var(--color-gray-300);
  font-family: var(--font-inter);
}
.figma-design-4png {
  width: 100%;
  position: relative;
  background-color: var(--color-gray-400);
  overflow: hidden;
  line-height: normal;
  letter-spacing: normal;
}
@media screen and (max-width: 1125px) {
  .groups1 {
    flex: 1;
  }
  .gallery {
    margin-left: 0;
  }
  .root1 {
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 1050px) {
  .trend {
    font-size: 55px;
    line-height: 61px;
  }
  .report {
    font-size: 62px;
  }
}
@media screen and (max-width: 750px) {
  .gallery,
  .groups1 {
    box-sizing: border-box;
    min-width: 100%;
  }
  .groups1 {
    gap: 77px;
    padding: 36px var(--padding-21xl) 25px;
  }
  .gallery {
    padding-bottom: var(--padding-2xl);
  }
}
@media screen and (max-width: 450px) {
  .trend {
    font-size: 33px;
    line-height: 41px;
  }
  .report {
    font-size: 46px;
  }
  .write-a-signific-container {
    font-size: 17px;
  }
  .briefly-elaborat-parent {
    gap: var(--gap-mid);
  }
  .groups1 {
    gap: var(--gap-19xl);
  }
}
